import React, { useState, useEffect } from 'react';
import Layout from './MainLayout';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from './Card';
import { getCategories, getFilteredProducts } from './apiCore';
import { makeStyles } from '@material-ui/core/styles';
import AddIcon from '@material-ui/icons/Add';

import { prices } from './fixedPrices';

const Main = () => {
  const [myFilters, setMyFilters] = useState({
    filters: { category: [], price: [] },
  });
  const [categories, setCategories] = useState([]);
  const [error, setError] = useState(false);
  const [limit, setLimit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [filteredResults, setFilteredResults] = useState([]);
  const [cartSelected, setCartSelected] = useState(false);
  const [price, setPrice] = useState(0);

  const init = () => {
    getCategories().then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        setCategories(data);
      }
    });
  };

  const randomizeSelect = (data) => {
    for (let i = 0; i < data.length; i ++) {
      if (Math.random() < 0.5) data[i].selected = true;
    }
  }

  const loadFilteredResults = (newFilters) => {
    // console.log(newFilters);
    getFilteredProducts(skip, limit, newFilters).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        randomizeSelect(data.data);
        updateFilterResults(data.data);
        setSize(data.size);
        setSkip(0);
      }
    });
  };
  const loadMore = () => {
    let toSkip = skip + limit;
    // console.log(newFilters);
    getFilteredProducts(toSkip, limit, myFilters.filters).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        // randomizeSelect(data.data);
        updateFilterResults([...filteredResults, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <AddIcon />
        </Button>
      )
    );
  };
  useEffect(() => {
    init();
    loadFilteredResults(skip, limit, myFilters.filters);
  }, []);

  const handlePrice = (value) => {
    const data = prices;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value)) {
        array = data[key].array;
      }
    }
    return array;
  };

  const shopItemClicked = (idx) => {
    filteredResults[idx].selected = !filteredResults[idx].selected;
    updateFilterResults([...filteredResults]);
  }

  const updateFilterResults = (results) => {
    setFilteredResults(results);
    console.log(results)
    const price = results.filter(itm => itm.selected).map(itm => itm.price).reduce((price, itm) => price + itm)
    console.log(price);
    setPrice(price);
  }

  return (
    <Layout
      title='Shop page'
      description='Search and find books'
      className='container-fluid'
      cartSelected={cartSelected}
      price={price}
      cartToggle={() => setCartSelected(!cartSelected)}
    >
      <div>
        <div className='d-flex flex-wrap justify-content-between'>
          {filteredResults.map((product, i) => (
            (!cartSelected || product.selected) &&
            <div key={i} className={'shop-item ' + (product.selected ? 'selected' : '' )}>
              <Card product={product}  shopItemClicked={() => shopItemClicked(i)}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
    </Layout>
  );
};

export default Main;
  